import { useSpring, animated, config } from 'react-spring'






export default function Loading_screen({visible_flag}){

  const spinner = useSpring({

    loop: true,
    from: { rotateZ:180+45, x:-70/2 },
    to: { rotateZ:-180+45 },
    reset: true,
    config: config.molasses,
  })

return(
  <div style={{width:'100vw',height:'100vh'}}>
    <animated.h1 style={{position:'absolute', top:'40%', visibility:visible_flag, width:'100vw', textAlign: 'center',  color:'white'}}>
      <p>LOADING...</p>
      <p>Dandepaz.net</p>
    </animated.h1>
    <animated.div style={{
      position:'relative',
      width:'65px',
      height:'65px',
      left: '50%',
      top:'32%',
      borderRadius: '15px',
      backgroundColor:'#C2B280',
      ...spinner,
      }}/>


  </div >
)
}
